<template>
  <div class="todolist-page">
    <!--  -->
    <p
      class="iconfont icon-xiangzuoshouqi"
      :class="{ leftBoxSwitch }"
      @click="leftBoxSwitch = !leftBoxSwitch"
    ></p>
    <!--  -->
    <div class="todolist-box-l" :class="{ leftBoxSwitch }">
      <div class="todolist-add-box">
        <!--  -->
        <div class="func-box">
          <div class="func-btn">
            <p class="ipt_zzc_b"></p>
            <input
              class="downloade_export_ipt"
              title=""
              type="file"
              accept=".txt"
              @change="upfile($event)"
            />
          </div>
          <p class="func-btn out" @click="exportLocal"></p>
          <p class="func-btn empty-all" @click="emptyAllTaskList"></p>
        </div>
        <!--  -->
        <div class="add-box">
          <addTask @addTask="addTask"></addTask>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="todolist-box-R" :class="{ leftBoxSwitch }">
      <taskList
        :taskListData="taskListData"
        @deletedTaskHome="deletedTaskHome"
        @changeState="changeState"
      ></taskList>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance
} from 'vue';
import { getLS, setLS, removeLS } from '@/utils/common.js';
import addTask from './addTask.vue';
import taskList from './taskList.vue';
import { STATE } from './config.js';
export default {
  name: '',
  components: {
    addTask,
    taskList
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      leftBoxSwitch: false, //
      taskListData: {}, //
      exportLocalTips: '()'
    });

    onBeforeMount(() => {});
    onMounted(() => {
      // mitt
      proxy.$bus.on('editTask', editTask);
    });
    // 
    const getAllTaskData = () => {
      for (let key in STATE) {
        data.taskListData[STATE[key]] = getLS(STATE[key]);
      }
      // console.log(data.taskListData);
    };

    getAllTaskData();
    // 
    const addTask = (task) => {
      // 
      if (
        task &&
        task.matter_state &&
        task.matter_id &&
        task.matter_date &&
        task.matter_title &&
        task.matter_grade &&
        task.matter_label
      ) {
        const state = task.matter_state; //
        data.taskListData[state].unshift(task); //
        setLS(state, data.taskListData[state]); //
      } else {
        console.log('');
      }
    };
    // 
    const deletedTaskHome = (task) => {
      const state = task.matter_state; //
      const targetArr = data.taskListData[state]; //
      targetArr.forEach((item, index) => {
        //id
        if (item.matter_id === task.matter_id) {
          targetArr.splice(index, 1);
        }
      });
      setLS(state, data.taskListData[state]); //
    };
    // 
    const changeState = (val) => {
      const targetObj = val[0]; //
      const targetState = val[1]; //
      deletedTaskHome(targetObj); //
      targetObj.matter_state = targetState; //
      addTask(targetObj); //
    };

    // 
    const emptyAllTaskList = () => {
      proxy
        .h_confirm({
          title: '',
          content: ' ',
          style: {
            box: {
              top: 300,
              bgColor: '#fff',
              width: 360,
              padding: 10,
              borderRadius: 4
            },
            content: {
              conColor: 'red'
            }
          }
        })
        .then(() => {
          // 
          for (let key in STATE) {
            removeLS(STATE[key]);
          }
          //
          getAllTaskData();
        });
    };
    // 
    const editTask = (val) => {
      // 
      const editList = data.taskListData[val.matter_state];
      // matter_id
      editList.forEach((item, index) => {
        if (item.matter_id === val.matter_id) {
          // 
          editList.splice(index, 1, val);
        }
      });
      setLS(val.matter_state, editList); //
    };

    // txt
    const upfile = (e) => {
      let resultFile = e.target.files[0]; //1
      // 
      if (resultFile) {
        //  FileReader 
        let reader = new FileReader();
        // , utf-8
        reader.readAsText(resultFile, 'UTF-8');
        // , onload , .
        reader.onload = (e) => {
          let fileContent = e.target.result; //
          addTaskFiles(fileContent); //
        };
      }
    };
    // 
    const addTaskFiles = (fileContent) => {
      let idList = []; //id
      for (let key in data.taskListData) {
        data.taskListData[key].forEach((task) => {
          idList.push(task.matter_id);
        });
      }

      // 
      const arr = fileContent.split(/[(\r\n)\r\n]+/);
      if (!arr[0].includes(data.exportLocalTips)) {
        console.log('');
        return;
      }
      if (arr.length === 4) {
        arr.shift(); //
        arr.forEach((item) => {
          let taskItem = JSON.parse(item);
          // 
          if (taskItem.length) {
            // id
            taskItem.forEach((append) => {
              if (idList.length) {
                // idid
                if (idList.includes(append.matter_id)) return;
                // 
                addTask(append);
              } else {
                // 
                addTask(append);
              }
            });
          }
        });
      } else {
        console.log('');
      }
    };

    // 
    const exportLocal = () => {
      // title
      const title =
        data.exportLocalTips +
        'TodoList' +
        new Date().toLocaleString();
      let allStr = title;
      // 
      for (let key in STATE) {
        allStr += '\r\n' + JSON.stringify(getLS(STATE[key]));
      }
      //
      const export_blob = new Blob([allStr]);
      //a
      const save_link = document.createElement('a');
      save_link.href = window.URL.createObjectURL(export_blob);
      //
      save_link.download = 'TodoList.txt';
      fakeClick(save_link);
    };

    // txt
    const fakeClick = (obj) => {
      const ev = document.createEvent('MouseEvents');
      ev.initMouseEvent(
        'click',
        true,
        false,
        window,
        0,
        0,
        0,
        0,
        0,
        false,
        false,
        false,
        false,
        0,
        null
      );
      obj.dispatchEvent(ev);
    };
    return {
      upfile,
      exportLocal,
      editTask,
      addTask,
      getAllTaskData,
      deletedTaskHome,
      changeState,
      emptyAllTaskList,
      ...toRefs(data)
    };
  }
};
</script>
